*{
  margin:0;
}
.bin2dec{
  background:#3c3846;
  color:#FFF;
  display:flex;
  justify-content:center;
  align-items:center;
  flex-direction:column;
  height:100vh;
  text-align:center;
}
p{
  margin:.7rem 0;
}
p.success{
  color:green;
}
p.danger{
  color:#bb2e3d;
}
input{
  padding:1rem ;
  border-radius:5px;
  border:none;
  margin: 1rem 0;
  width: 40%;
}
input:focus{
	border: 3px solid #b2d3e1;
    box-shadow: 0px 1px 10px 3px #b2d3e1;
    outline: none;
}